$color_1: #aaa;
$color_2: #888;
$color_3: #ff6d6d;
$color_4: #000;
$color_5: #8d00b1;
$color_6: #333;
$color_7: #ddd;
$color_8: #fff;
$color_9: #666;
$color_10: #17a8e3;
$background-color_1: #ddd;
$background-color_2: #fff;
$background-color_3: #e6e6e6;
$background-color_4: #17a8e3;
$background-color_5: #000;
$background-color_6: #666;
$background-color_7: #d70;
$background-color_8: #f57d00;
$background-color_9: #ea4c89;
$background-color_10: #007ee5;
$background-color_11: #f6682f;
$background-color_12: #3b5999;
$background-color_13: #ff0084;
$background-color_14: #f94877;
$background-color_15: #ecbb2e;
$background-color_16: #eb4924;
$background-color_17: #dd4b39;
$background-color_18: #e4405f;
$background-color_19: #0077b5;
$background-color_20: #bd081c;
$background-color_21: #ee4056;
$background-color_22: #bc0b0b;
$background-color_23: #ff5700;
$background-color_24: #00aff0;
$background-color_25: #1ed760;
$background-color_26: #34465d;
$background-color_27: #55acee;
$background-color_28: #1ab7ea;
$background-color_29: #25d366;
$background-color_30: #21759b;
$background-color_31: #437fbb;
$background-color_32: #cd201f;
$background-color_33: #0073aa;
$background-color_34: #085;
$background-color_35: #fafafa;
$background-color_36: #8d00b1;
$background-color_37: transparent;
$background-color_38: rgba(255, 255, 255, .98);
$background-color_39: rgba(225, 246, 255, .98);
$border-color_1: #ddd;
$border-color_2: #888;
$border-color_3: #000;

.branda-admin-page {
	.sui-wrap-branda {
		.simple-option-select2 {
			select {
				min-width: 300px;
				width: 40%;
			}
		}
		.simple-option-select2-ajax {
			select {
				min-width: 300px;
				width: 40%;
			}
		}
		.branda-empty {
			padding: 0 10px 30px;
			text-align: center;
			h2.branda-image {
				margin-top: 0;
				&:before {
					content: "";
					display: block;
					width: 128px;
					height: 128px;
					background: transparent none no-repeat 50%;
					margin: 30px auto;
				}
			}
			h2.branda-image.branda-image-confused {
				&:before {
					//background-image: url(../images/branda/confused@1x.png);
				}
			}
			h2.branda-image.branda-image-empty {
				&:before {
					background-image: url(../images/branda/empty-office-tray.png);
				}
			}
		}
		.branda-divider {
			border-bottom: 1px solid #e6e6e6;
			margin: 21px 15px 0;
		}
		.branda-accordion-body {
			.sui-row {
				&:last-child {
					.branda-divider {
						display: none;
					}
				}
			}
		}
		.branda-images-images-override {
			max-width: 500px;
		}
		>div.sui-header {
			>h1.sui-header-title {
				text-transform: uppercase;
			}
		}
		.sui-actions-left.top {
			.select-container {
				margin-right: 10px;
			}
		}
		.sui-accordion-item-header {
			.check-column {
				max-width: 55px;
			}
		}
		canvas.branda-focal {
			width: 302px;
			height: 202px;
			border: 1px solid #e6e6e6;
			margin-top: 10px;
			background-color: $background-color_1;
			background-size: 150%;
			background-repeat: no-repeat;
		}
		.branda-not-affected {
			opacity: .5;
		}
		.check-column {
			max-width: 55px;
			label.sui-checkbox {
				min-width: 18px;
			}
		}
		.sui-modal.sui-modal-sm.branda-test-email {
			.sui-box {
				.sui-box-header {
					padding-bottom: 0;
				}
				.sui-box-body {
					padding-bottom: 7px;
				}
				.sui-box-footer {
					padding-bottom: 40px;
					background-repeat: no-repeat;
					background-position: 50% 100%;
					background-size: 140px auto;
					.sui-button {
						.sui-icon-loader {
							position: static;
						}
					}
				}
			}
		}
		.sui-modal {
			#branda-copy-settings-db-error-page {
				.sui-box {
					.sui-box-body {
						.sui-label {
							color: $color_1;
							font-size: 12px;
						}
					}
				}
			}
			.branda-test-email {
				.sui-box {
					.sui-box-body {
						.sui-label {
							color: $color_1;
							font-size: 12px;
						}
					}
				}
			}
		}
		.ace_emptyMessage {
			color: $color_1;
		}
		.simple-option {
			.sui-tab-item.branda-icon.active {
				svg {
					path {
						fill: #17a8e3;
					}
				}
			}
			.sui-tabs-menu {
				.sui-form-control {
					padding: 9px 14px;
					margin-left: 10px;
					border-radius: 4px;
					-webkit-border-radius: 4px;
					-moz-border-radius: 4px;
					font-size: 15px;
				}
			}
		}
		.simple-option.simple-option-sui-tab-icon {
			div[for=simple_options_design_background_position_x] {
				max-width: 355px;
			}
			div[for=simple_options_design_background_position_y] {
				max-width: 355px;
			}
		}
		#branda-permissions-users {
			border: 0;
			.sui-box-builder-body {
				padding: 0;
				background-color: $background-color_2;
			}
			.sui-builder-field {
				border: 1px solid #e6e6e6;
				padding: 10px 15px;
				margin: 0 0 10px;
				min-height: unset;
				&:hover {
					box-shadow: none;
				}
				.sui-builder-field-label {
					span {
						&:last-child {
							font-weight: 400;
							color: $color_2;
						}
					}
				}
			}
			.branda-button-hover-red {
				width: 9%;
				&:hover {
					color: $color_3;
				}
			}
			.branda-permissions-user-title {
				width: 30%;
			}
			.branda-field-image {
				width: 30px;
				height: 30px;
				margin-right: 10px;
				.branda-circle-image {
					width: 28px;
					height: 28px;
					-ms-flex: 0 0 30px;
					flex: 0 0 30px;
					border-radius: 30px;
				}
			}
		}
		#branda-permissions-add-user {
			span[role=presentation] {
				display: none;
			}
			.sui-loading-text-adding {
				display: none;
				i {
					&:before {
						animation: spin 1.3s linear infinite;
					}
				}
			}
		}
		ul.branda-small-list {
			padding-left: 17px;
			margin: 0;
			list-style: disc;
			color: $color_4;
			>li {
				>span.sui-settings-label {
					font-size: 13px;
				}
			}
		}
		.branda-new-features {
			h3.sui-box-title {
				white-space: normal;
				overflow: visible;
			}
		}
	}
	.sui-wrap-branda.no-branda {
		.branda-empty {
			h2.branda-image {
				&:before {
					background-image: none;
					padding-top: 0;
					display: none;
				}
			}
			h2.branda-image.branda-image-confused {
				&:before {
					background-image: none;
					padding-top: 0;
					display: none;
				}
			}
		}
	}
	#branda-dashboard-widget-summary {
		.sui-icon-magnifying-glass-search {
			top: -28px;
			left: 13px;
		}
		.sui-summary-segment {
			&:last-child {
				.sui-summary-details {
					padding-top: 10px;
				}
				.sui-control-with-icon {
					display: inline-table;
					float: right;
				}
			}
		}
	}
	#branda-dashboard-widget-upsell {
		.sui-icon-check {
			&:before {
				color: $color_5;
				margin-right: 10px;
				vertical-align: sub;
			}
		}
		.sui-box-body {
			li {
				font-weight: 500;
				color: $color_6;
			}
		}
		.branda_u_logo {
			background-repeat: no-repeat;
			background-position: center;
			background-size: 20px auto;
			width: 36px;
			height: 34px;
		}
	}
	#branda-dashboard-widget-admin {
		.dashicons {
			margin-right: 10px;
			line-height: 20px;
		}
	}
	#branda-dashboard-widget-modules {
		.sui-box-head {
			padding: 20px 30px;
			h2 {
				color: $color_6;
				i {
					margin-right: 10px;
					&:before {
						font-size: 1em;
						color: $color_6;
					}
				}
				.dashicons {
					font-size: 26px;
					line-height: 35px;
					margin-right: 10px;
					width: 22px;
				}
			}
		}
		.sui-table--button {
			width: 80px;
		}
		.sui-table--status {
			width: 95px;
		}
		.sui-table--configure {
			width: 73px;
		}
		div.branda-status-elements {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
		.branda-module-for-pro {
			opacity: .5;
		}
		span.branda-module-status {
			display: inline-block;
			background-color: $background-color_3;
			width: 10px;
			height: 10px;
			border-radius: 5px;
			-webkit-border-radius: 5px;
			-moz-border-radius: 5px;
		}
		span.branda-module-status.module-status-active {
			background-color: $background-color_4;
		}
	}
	#branda-dashboard-search-no-results {
		.branda-empty {
			padding-top: 50px;
			padding-bottom: 60px;
		}
	}
	#branda-manage-all-modules {
		.sui-box-header {
			//background-image: url(../../assets/images/branda/circle-confused@2x.png);
            background-image: url(../../assets/images/branda/circle-switch.png);
			background-position: 50% 0;
			background-repeat: no-repeat;
			background-size: 130px 130px;
			display: block;
			padding-top: 140px;
			text-align: center;
			padding-top: 200px;
			background-position-y: 45px;
			.sui-header-title {
				text-align: center;
				text-transform: none;
				font-size: 22px;
			}
			.sui-notice {
				max-width: 600px;
				margin-left: auto;
				margin-right: auto;
			}
		}
		.sui-box-footer {
			justify-content: center;
			.branda-save-all {
				.sui-loading {
					position: static;
				}
			}
		}
	}
	.branda-welcome-step2 {
		#branda-welcome {
			.sui-box-header {
				//background-image: url(../../assets/images/branda/circle-confused@2x.png);
				background-position: 50% 0;
				background-repeat: no-repeat;
				background-size: 130px 130px;
				display: block;
				padding-top: 140px;
				text-align: center;
				padding-top: 200px;
				background-position-y: 45px;
				.sui-header-title {
					text-align: center;
					text-transform: none;
					font-size: 22px;
				}
				.sui-notice {
					max-width: 600px;
					margin-left: auto;
					margin-right: auto;
				}
			}
			.sui-box-footer {
				justify-content: center;
				.branda-save-all {
					.sui-loading {
						position: static;
					}
				}
			}
		}
	}
	.sui-wrap.sui-wrap-branda.branda-import-import {
		.sui-header {
			//background-image: url(../../assets/images/branda/circle-confused@2x.png);
			background-position: 50% 0;
			background-repeat: no-repeat;
			background-size: 130px 130px;
			display: block;
			padding-top: 140px;
			text-align: center;
			.sui-header-title {
				text-align: center;
				text-transform: none;
			}
			.sui-notice {
				max-width: 600px;
				margin-left: auto;
				margin-right: auto;
			}
		}
		.sui-header.branda-import-error {
			//background-image: url(../../assets/images/branda/confused@2x.png);
		}
		.branda-import-err {
			.sui-container {
				border: 1px solid #e6e6e6;
				padding: 5px 30px 30px 30px;
				margin: 60px 75px 30px 75px;
				.sui-label {
					margin-bottom: 10px;
				}
				.sui-row {
					justify-content: center;
					.sui-col {
						max-width: 200px;
					}
				}
			}
		}
	}
	.sui-wrap.sui-wrap-branda.branda-import-import.branda-import-error {
		.sui-header {
			//background-image: url(../../assets/images/branda/confused@2x.png);
		}
	}
	.sui-wrap.sui-wrap-branda.sui-color-accessible {
		#branda-dashboard-widget-modules {
			span.branda-module-status {
				background-color: $background-color_2;
				border: 1px solid #000;
			}
			span.branda-module-status.module-status-active {
				background-color: $background-color_5;
			}
		}
	}
	.sui-wrap.sui-wrap-branda.no-branda {
		#branda-manage-all-modules {
			.sui-box-header {
				background-image: none;
				padding-top: 15px;
			}
		}
		.branda-welcome-step2 {
			#branda-welcome {
				.sui-box-header {
					background-image: none;
					padding-top: 15px;
				}
			}
		}
	}
	.sui-wrap.sui-wrap-branda.no-branda.branda-import-import {
		.sui-header {
			background-image: none;
			padding-top: 15px;
		}
		.sui-header.branda-import-error {
			background-image: none;
			padding-top: 15px;
		}
	}
	#wpbody-content {
		.sui-wrap.sui-wrap-branda.no-branda {
			.sui-unbranded {
				.sui-summary-segment {
					padding-left: 15px;
				}
			}
		}
	}
	.sui-wrap.sui-wrap-branda {
		.sui-box-body {
			.sui-modal.branda-social-logo-add-dialog {
				.sui-modal-content {
					max-width: 700px;
					.sui-box-body {
						padding: 30px 0 0;
						.sui-description {
							padding: 0 30px;
						}
						.sui-box-selectors {
							margin: 0;
							padding: 20px;
							ul {
								margin-top: 0;
								margin-bottom: 0;
								justify-content: center;
								li {
									flex: auto;
									min-width: 120px;
									max-width: 120px;
									.branda-social-logo-container {
										flex-wrap: wrap;
										justify-content: center;
										.social-logo {
											margin: 0;
										}
										.social-media-title {
											display: block;
											padding-top: 5px;
											text-align: center;
											width: 100%;
										}
									}
									.sui-box-selector {
										input+span {
											padding: 14px 10px 15px 10px;
										}
									}
								}
							}
						}
					}
					.sui-box-footer {
						border-top: 0;
					}
				}
			}
			.branda-social-logos-main-container {
				.branda-social-logo-remove-container {
					margin-right: 0;
					padding-right: 0;
					opacity: 0;
					&:after {
						border-right: 1px solid #e6e6e6;
						content: " ";
						height: 20px;
						margin-left: 14px;
						width: 0;
					}
				}
				.sui-icon-drag {
					&:before {
						color: $color_7;
					}
				}
				.sui-accordion-item-header {
					&:hover {
						.branda-social-logo-remove-container {
							opacity: 1;
						}
						.sui-icon-drag {
							&:before {
								color: $color_2;
							}
						}
					}
				}
			}
			.sui-button-magenta {
				background-color: $background-color_36;
			}
			.sui-tabs-content {
				.branda-tab-design-avatar_show {
					border: 0;
					padding: 0;
					padding-top: 10px;
				}
			}
			.branda-section-theme {
				.branda-big-button {
					border-color: $border-color_1;
					background-color: $background-color_37;
					color: $color_2;
					font: 500 12px/16px Roboto, Arial, sans-serif;
					letter-spacing: -.25px;
					align-items: center;
					display: inline-flex;
					cursor: pointer;
					border-style: solid;
					border-radius: 4px;
					transition: all .3s ease;
					background-size: cover;
					background-repeat: no-repeat;
					border-width: 1px;
					height: 160px;
					text-transform: initial;
					width: 220px;
					&:hover {
						border-color: $border-color_2;
					}
					span {
						text-align: center;
						width: 100%;
						i {
							display: block;
							margin: 10px auto;
							&:before {
								font-size: 1.4em;
							}
						}
					}
				}
				.branda-big-button.branda-has-theme {
					&:hover {
						color: $color_6;
						background-color: $background-color_38;
						background-image: none !important;
						i {
							&::before {
								color: $color_6;
							}
						}
					}
				}
			}
			.branda-no-sui-columns {
				display: block;
			}
			.sui-modal.branda-choose-template-dialog {
				.sui-box-selector {
					display: initial;
				}
				.sui-box-body {
					padding-left: 0;
					padding-right: 0;
					.sui-notice {
						margin-left: 30px;
						margin-right: 30px;
					}
				}
				.branda-template-container {
					border-radius: 4px;
					-webkit-border-radius: 4px;
					-moz-border-radius: 4px;
					box-shadow: 0 2px 7px rgba(0, 0, 0, .05);
					-webkit-box-shadow: 0 2px 7px rgba(0, 0, 0, .05);
					-moz-box-shadow: 0 2px 7px rgba(0, 0, 0, .05);
					display: block;
					width: 220px;
					height: 160px;
					background-size: cover;
					background-color: $background-color_2;
					span {
						display: none;
						text-align: center;
						width: 100%;
					}
					span.email-template-icon {
						margin: 40px auto 0 auto;
						i {
							margin: 0;
							&:before {
								font-size: 1.4em;
							}
						}
					}
					span.login-screen-icon {
						margin: 40px auto 0 auto;
						i {
							margin: 0;
							&:before {
								font-size: 1.4em;
							}
						}
					}
					span.email-template-title {
						padding-top: 5px;
					}
					span.login-screen-title {
						padding-top: 5px;
					}
					&:hover {
						background-color: $background-color_38;
						background-image: none !important;
						span {
							display: block;
						}
					}
				}
				.branda-login-screen-template-scratch {
					.branda-template-container {
						span {
							display: block;
						}
					}
				}
				.sui-notice {
					p {
						text-align: left;
					}
				}
			}
			.sui-modal.branda-choose-template-dialog.branda-login-screen-choose-template-dialog {
				.branda-login-screen-li {
					flex: 0;
					min-width: 240px;
				}
			}
			.sui-modal.branda-choose-template-dialog.branda-email-template-choose-template-dialog {
				.sui-modal-content {
					max-width: 820px;
				}
				.branda-template-container {
					width: 160px;
				}
				.branda-email-template-scratch {
					color: $color_2;
					span {
						display: block;
					}
				}
				.branda-email-template-li {
					flex: 0;
					min-width: 180px;
				}
			}
			.sui-modal {
				.sui-box-selectors {
					ul {
						align-content: flex-start;
						li {
							flex: 0;
						}
					}
					.branda-selected {
						.branda-login-screen-container {
							background-color: $background-color_39;
							background-image: none !important;
							span {
								display: block;
							}
						}
						.branda-template-container {
							background-color: $background-color_39;
							background-image: none !important;
							span {
								display: block;
