		.branda-dialog-new {
			.sui-box-header {
				.sui-box-title {
					span.branda-edit {
						display: none;
					}
					span.branda-new {
						display: inline;
					}
				}
			}
			.sui-box-footer {
				button.branda-save {
					span.branda-edit {
						display: none;
					}
					span.branda-new {
						display: inline;
					}
				}
			}
		}
	}
}
